import { ChangeDetectionStrategy, Component } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { FORM } from '@app/modules/newscenter/newscenter.form';
import { ClubConfigModel } from '@myclapp/myclapp-server';
import {BehaviorSubject} from 'rxjs';
import {BaseConfigComponent} from '../../../../../helpers/base-config.component';

import {NewscenterService, UserService} from '../../../../../services';

@Component({
    selector: 'sb-newscenter',
    changeDetection: ChangeDetectionStrategy.OnPush,
    templateUrl: './newscenter.component.html',
    styleUrls: ['./newscenter.component.scss'],
})
export class NewscenterComponent extends BaseConfigComponent<ClubConfigModel>{
    protected _form: FormGroup;
    protected _mappedModel$: BehaviorSubject<ClubConfigModel> = new BehaviorSubject<ClubConfigModel>(
        {
            "sporttotal_url" : "",
            "club_id" : -1,
            "homepage_url" : "",
            "fussballde_url" : "",
            "id" : -1,
            "twitter_id" : "",
            "youtube_id" : "",
            "facebook_id" : "",
            "instagram_id" : ""
        }
    );
    formLayout: any = FORM;

    constructor(private fb: FormBuilder, private newscenterService: NewscenterService, private userService: UserService) {
        super(NewscenterComponent.name);
        this._logger.debug('Constructing component')
        this._form = this.fb.group({
            facebook_id: [''],
            twitter_id: [''],
            instagram_id: [''],
            homepage_url: ['', Validators.required],
            sporttotal_url: ['', Validators.required],
            fussballde_url: ['', Validators.required],
            youtube_id: [''],
        });
        this._initialStatus$.next({status: 'pending'});
        this.newscenterService.get().subscribe(data => {
            this._initialStatus$.next({status: 'success'})
            this._mappedModel$.next(data)
            setTimeout(() => {
                this._initialStatus$.next({status: 'idle'})
            },300)
        })

        this._mappedModel$.subscribe(data => {
            this._logger.debug('_mappedModel updated ',data)
            for (const [key,value] of Object.entries(data)){
                this._form.controls[key]?.setValue(value)
            }
        })
    }

    protected _submitData(): void {
        const data: ClubConfigModel = {
            ...this._form.value,
            club_id: this.userService.club_id,
            id: this.userService.club_id
        }
        this.newscenterService.submit(data).subscribe(res => {
            this._logger.debug('Received sumbit result',res)
            this._submitStatus$.next({status: 'success'})
            setTimeout(() => {
                this._initialStatus$.next({status: 'idle'})
            },300)
            this._mappedModel$.next(res)
        })
    }

}
